import './Contents.css'
import ThemeButton from '../Button/Button'
function Wrp({heading,paragraf,buttonLink,buttonWrp}) {
    return(
        <div className="wrp">
            <h2>{heading}</h2>
            <p>{paragraf}</p>
            { buttonWrp ? <ThemeButton href={buttonLink} value={buttonWrp} /> : '' }
        </div>
    )
}

export default Wrp;