import ThemeButton from '../HelperComponents/Button/Button'
import './Headers.css'
function Hero() {
    return(
        <div className="hero-area section">
            <div className="container">
                <div className="col-md-8 offset-md-2 text-center">
                    <div 
                        className="hero-wrp" 
                        data-aos="zoom-in-up" 
                        data-aos-duration="3000"
                    >
                        <h2>Cheers to vodka without compromise and oceans <span>without plastics</span></h2>
                        <p>OVO Vodka is a new ultra-premium, gluten-free vodka that empowers those who enjoy it to help cleanse Our Five (V) Oceans simply by enjoying a drink.</p>
                    </div>
                </div>
            </div> 
            <ThemeButton href="#" value="Let's get started" />
        </div>
    )
}
export default Hero;