import Wrp from '../HelperComponents/wrp/Contents';
import './Blog.css'
import ThemeButton from '../HelperComponents/Button/Button'
function Singleblock({heading,paragraf,link,img,value}) {
    return(
        <div className="blog-block">
            <img src={img} alt=""/> 
            <Wrp 
                heading={heading} 
                paragraf={paragraf} 
                buttonLink={link}
                buttonWrp={value}
            />  
        </div>
    )
}
export default Singleblock;