import Wrp from '../HelperComponents/wrp/Contents';
import './Mission.css';
import MissionImg from '../imgs/second-img.png';
function Mission() {
    return(
        <div className="mission section">
            <div className="container">
                <div className="row align-items-center"> 
                    <div 
                        className="col-lg-4"
                        data-aos="fade-left" 
                        data-aos-duration="1000" 
                    >
                        <Wrp heading="Our mission" paragraf="Advocates of drinking for a cause, we’re creating a great tasting vodka infused with the spirit of goodwill. Join the OVOlution" />
                    </div>
                    <div 
                        className="col-lg-4"
                        data-aos="zoom-in-up" 
                        data-aos-duration="1000"
                        data-aos-delay="300"
                    >
                        <div className="mimssion-img">
                            <img src={MissionImg} alt=""/>
                        </div>
                    </div>
                    <div 
                        className="col-lg-4"
                        data-aos="fade-right" 
                        data-aos-duration="1000" 
                    >
                        <Wrp heading="Our mission" paragraf="Advocates of drinking for a cause, we’re creating a great tasting vodka infused with the spirit of goodwill.   <b>Join the OVOlution " />
                    </div>
                </div>
            </div>
        </div>
    )
}


export default Mission;