import './bluesea.css'
import Wrp from '../HelperComponents/wrp/Contents' 
function Bluesea() {
    return(
        <div className="bluesea section">
            <div className="container">
                <div className="row align-items-center">
                    <div className="col-lg-6">
                        <div 
                            className="iframe-video"
                            data-aos="zoom-out" 
                            data-aos-duration="1000" 
                        >
                            <iframe src="https://www.youtube.com/embed/cMNH4nmOims" title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
                        </div>
                    </div>
                    <div 
                        className="col-lg-6"
                        data-aos="fade-left" 
                        data-aos-duration="1000"
                        data-aos-delay="500"
                    >
                        <h3>Our charity partner</h3>
                        <Wrp 
                            heading="Clear Blue Sea" 
                            paragraf="The mission of our nonprofit partner is to cleanse the oceans of plastic pollution. Clear Blue Sea has innovated a Floating Robot for Eliminating Debris, FRED, to remove plastic from oceans, rivers, and bays. OVO Vodka donates $2 per bottle purchased to help actively remove plastics from marine environments around the world. Together, we are not just slowing plastic pollution, but remediating the oceans back to clear blue seas. " 
                            buttonLink="http://www.clearbluesea.org" 
                            buttonWrp="www.clearbluesea.org"
                        />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default  Bluesea;