import './OVOlution.css'
import Wrp from '../HelperComponents/wrp/Contents'
import BottoleImg from '../imgs/bottle.png'
function OVOlution() {
    return(
        <div className="OVOlution section">
            <div className="container">
                <div className="row align-items-center">
                    <div className="col-lg-6">
                        <div 
                            className="section-wrp"
                            data-aos="zoom-in-up" 
                            data-aos-duration="1000"
                            data-aos-delay="300"
                        >
                            <img src={BottoleImg} alt=""/>
                        </div>
                    </div>
                    <div 
                        className="col-lg-6"
                        data-aos="fade-left" 
                        data-aos-duration="1000"
                        data-aos-delay="500"
                    >
                        <Wrp 
                        heading="Join the OVOlution" 
                        paragraf="OVO Vodka is made from 100% high-grade American corn which is distilled 6 times for a purer distillate then filtered with activated carbon for a clean, refined, ultra premium taste. That’s our fancy way of saying that it’s unbelievably smooth, delicious, and gluten-free."  />
                    </div>
                </div>
            </div>
        </div>
    )
}

export default OVOlution;