import './bottole.css'
import Wrp from '../HelperComponents/wrp/Contents'
import Img from '../imgs/1.png'
import Img2 from '../imgs/2.png'
import Img3 from '../imgs/3.png'
function Bottles() {
    return(
        <div className="bottles section">
            <div className="container">
                <div className="row align-items-center">
                    <div 
                        className="col-lg-6"
                        data-aos="fade-right" 
                        data-aos-duration="1000"
                        ata-aos-delay="300"
                    >
                        <Wrp 
                            heading="Americans use 70 million
                            plastic bottles every day" 
                            paragraf="The plastic crisis is one of the greatest challenges facing planet earth. For every bottle of our ultra-premium OVO Vodka purchased, you’re responsible for removing the equivalent of 18 plastic bottles from our oceans. That averages 1 plastic bottle removed for every shot."  
                        />
                        <h4>Has a hangover ever felt so good?</h4>
                    </div>
                    <div className="col-lg-6">
                        <div className="galleryimages">
                            <img 
                                src={Img} alt=""
                                data-aos="fade-up" 
                                data-aos-duration="1000"
                                data-aos-delay="300"
                            /> 
                            <img 
                                src={Img2} alt=""
                                data-aos="fade-up" 
                                data-aos-duration="1000"
                                data-aos-delay="600"
                            /> 
                            <img 
                                src={Img3} alt=""
                                data-aos="fade-up" 
                                data-aos-duration="1000"
                                data-aos-delay="900"
                            /> 
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default  Bottles;