import './vodka.css'
import Wrp from '../HelperComponents/wrp/Contents'
import WebsiteImg from '../imgs/website.png'
function Vodka() {
    return(
        <div className="vodka section">
            <div className="container">
                <div className="row align-items-center">
                    <div 
                        className="col-lg-6"
                        data-aos="fade-up" 
                        data-aos-duration="1000" 
                    >
                        <Wrp 
                        heading="OVO is reinventing how  The world drinks vodka" 
                        paragraf="Each drop purchased goes towards cleaning up the world’s oceans by removing harmful plastics, one drink at a time. Working towards creating new industry standards and ending damaging cycles, OVO is dedicated to disrupting the industry by making some of the best tasting vodka and leaving the world better than we found it.  " 
                        buttonLink="#" 
                        buttonWrp="Read our story"/>
                    </div>
                    <div className="col-lg-6">
                        <div 
                            className="section-wrp"
                            data-aos="fade-up" 
                            data-aos-duration="1000"
                            data-aos-delay="500"
                        >
                            <img src={WebsiteImg} alt=""/>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default  Vodka;